.login {
  min-height: 97vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  & h2 {
    margin-bottom: 2rem;
  }

  & button {
    margin-top: 0.5rem;
  }
}
