@import "../../services/scss/color-palette.scss";

.container {
  min-height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pageHeader {
  width: 100%;
  min-height: 35vh;
  display: flex;
  justify-content: center;
  padding-block-start: 2rem;
}

.headerText {
  color: $beige-light;
  text-shadow: 5px 5px 10px black;
  text-align: center;
  align-self: center;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 0.5rem;

  &__about {
    border-radius: 10px;
    padding: 1rem;
    margin: 1rem 0rem;
    background-color: $beige-light;
  }

  &__addFood {
    margin: 5rem auto;
  }
}
